import React from 'react';
import Minesweeper from './dom_games/Minesweeper';
import Tetris from './dom_games/Tetris';


import { ThemeContextConsumer } from './themeContext';


const GameRadio = () => {

    return (
        <ThemeContextConsumer>
        {({domGame, click}) => (
        <div>                                             
            {domGame==='tetris' && <Tetris domGame={domGame}/>}
            {domGame==='minesweeper' && <Minesweeper domGame={domGame}/>}

            <h3>DOM Games</h3>
            <div className="radioBtnContainer">
                <div>
                    <label>
                        <input  type="radio" 
                                name="domGame" 
                                value="tetris" 
                                checked={domGame==="tetris"} 
                                onChange={click} />
                    Tetris</label>
                </div>

                <div>
                    <label>
                        <input  type="radio" 
                                name="domGame" 
                                value="minesweeper" 
                                checked={domGame==="minesweeper"} 
                                onChange={click} />
                    MineSweeper</label>
                </div>

            </div>	
            <br />

        </div>
        )}
        </ThemeContextConsumer>
    )
}

export default GameRadio;