import React from 'react';
import EvenThree from './mask_types/EvenThree';
import WideThree from './mask_types/WideThree';
import WiderThree from './mask_types/WiderThree';
import EvenFour from './mask_types/EvenFour';
import EvenFive from './mask_types/EvenFive';
import Eclipse from './mask_types/Eclipse';
import { ThemeContextConsumer } from './themeContext';



const MaskRadio = () => {
    return (
        <ThemeContextConsumer>
        {({maskTheme, click})=> (
        <div> 
            <div className="descriptionContainer">
                <p className="leftText">Some image masks that I created while experimenting with SASS in order to become more familiar with its functionalities.</p>
            </div>

                
                <div className="maskContainerSection">
                    <div className="maskedImageDisplay _4to3lg">
                        <img src="./img/masks/mountain-view-golden-hour-photography-733168.jpg" alt="#" />
                        {maskTheme==='even3' && <EvenThree />}
                        {maskTheme==='even4' && <EvenFour />}
                        {maskTheme==='even5' && <EvenFive />}
                        {maskTheme==='wide3' && <WideThree />}
                        {maskTheme==='wider3' && <WiderThree />}
                        <div className="triangle up" style={{display: maskTheme==='triU' ? 'flex' : 'none'}}></div>
                        <div className="triangle down" style={{display: maskTheme==='triD' ? 'flex' : 'none'}}></div>
                    </div>
                </div>

                <h3>Image Masks</h3>
                <div className="radioBtnContainer">
                    <div>
                        <label>
                            <input type="radio" 
                                    name="maskTheme" 
                                    value="even3" 
                                    checked={maskTheme==="even3"} 
                                    onChange={click} />
                        3 panel, even</label>
                    </div>
                    <div>
                        <label>
                            <input type="radio" 
                                    name="maskTheme" 
                                    value="even4" 
                                    checked={maskTheme==="even4"} 
                                    onChange={click} />
                        4 panel, even</label>
                    </div>
                    <div>
                        <label>
                            <input type="radio" 
                                    name="maskTheme" 
                                    value="even5" 
                                    checked={maskTheme==="even5"} 
                                    onChange={click} />
                        5 panel, even</label>
                    </div>
                    <div>
                        <label>
                            <input type="radio" 
                                    name="maskTheme" 
                                    value="wide3" 
                                    checked={maskTheme==="wide3"} 
                                    onChange={click} />
                        3 panel, wide</label>
                    </div>
                    <div>
                        <label>
                            <input type="radio" 
                                    name="maskTheme" 
                                    value="wider3" 
                                    checked={maskTheme==="wider3"} 
                                    onChange={click} />
                        3 panel, wider</label>
                    </div>
                    <div>
                        <label>
                            <input type="radio" 
                                    name="maskTheme" 
                                    value="triU" 
                                    checked={maskTheme==="triU"} 
                                    onChange={click} />
                        upward triangle</label>
                    </div>
                    <div className="last">
                        <label>
                            <input type="radio" 
                                    name="maskTheme" 
                                    value="triD" 
                                    checked={maskTheme==="triD"} 
                                    onChange={click} />
                        downward triangle</label>
                    </div>
                </div>
                
 
            {/** static masked images */}
            <div className="maskContainerSection">
                <div className="maskContainer _4to3">
                    <img src="./img/masks/mountain-view-golden-hour-photography-733168.jpg" alt="#" />
                    < EvenFour />
                </div>

                <div className="maskContainer _4to3">
                    <img src="./img/masks/pexels-photo-247431Pixabay1000.jpeg" alt="#" />
                    < WiderThree />
                </div>

                <div className="maskContainer _4to3">
                    <img src="./img/masks/trees-in-forest-during-sunset-247421.jpg" alt="#" />
                    < EvenThree />
                </div>
            </div>


            <div className="maskContainerSection">
                <div className="maskContainer _1to1">
                    <img src="./img/masks/nature-animal-wolf-wildernessPixabay.jpg" alt="#" /> 
                    <div className="triangle down"></div> 
                </div>

                <div className="maskContainer  _1to1lg">
                    <img src="./img/masks/pexels-photo-2131828QuangNguyenVinh650.jpeg" alt="#" /> 
                    <div className="triangle up"></div>
                </div>

                <div className="maskContainer _1to1">
                    <img src="./img/masks/silver-bordered-fritillary-butterfly-nature-orangePixabay.jpg" alt="#" />
                    <div className="triangle down"></div>
                </div>
            </div>


            <div className="maskContainerSection">
                <div className="maskContainer _1to1sm">
                    <img className="circle" src="./img/masks/earth-spaceJaymantri1000.jpg" alt="#" />
                </div>

                <div className="maskContainer _1to1lg">
                    <img className="circle" src="./img/masks/pexels-photo-2885320RobertoNickson.jpeg" alt="#" />
                    < Eclipse />
                </div>

                <div className="maskContainer _1to1sm">
                    <img className="circle" src="./img/masks/pexels-photo-55787DonaldTong.jpeg" alt="#" />
                </div>
            </div>
        </div>
        )}
        </ThemeContextConsumer>
    )
}



export default MaskRadio;