import React from 'react';
import OceanGrid from './grid_designs/OceanGrid';
import AviaryGrid from './grid_designs/AviaryGrid';
import BlogTrail from './grid_designs/BlogTrail';
import { ThemeContextConsumer } from './themeContext';


const GridRadio = () => {
    return (
        <ThemeContextConsumer>
        {({gridTheme, click}) => (
        <div>                                             
            {gridTheme==='sandWater' && <OceanGrid />}
            {gridTheme==='birds' && <AviaryGrid />}
            {gridTheme==='trail' && <BlogTrail />}


            <h3>Some Grid Compositions</h3>
            <div className="radioBtnContainer">
                <div>
                    <label>
                        <input  type="radio" 
                                name="gridTheme" 
                                value="trail" 
                                checked={gridTheme==="trail"} 
                                onChange={click} />
                    Blogtrail</label>
                </div>
                <div>
                    <label>
                        <input  type="radio" 
                                name="gridTheme" 
                                value="sandWater" 
                                checked={gridTheme==="sandWater"} 
                                onChange={click} />
                    Ocean</label>
                </div>

                <div>
                    <label>
                        <input  type="radio" 
                                name="gridTheme" 
                                value="birds" 
                                checked={gridTheme==="birds"} 
                                onChange={click} />
                    Aviary</label>
                </div>

            </div>	
            <br />

        </div>
        )}
        </ThemeContextConsumer>
    )
}

export default GridRadio;