import React from 'react';
import Prism from './animations/Prism';
import Helix from './animations/Helix';
import Darklight from './animations/Darklight';
import Leafdance from './animations/Leafdance';
import { ThemeContextConsumer } from './themeContext';


const AnimationRadio = () => {
    return (
        <ThemeContextConsumer>
        {({animation, click}) => (
        <div className="design-container">
            <div>                                            
                {animation==='prism' && <Prism numCol={5} numRow={5}/>}
                {animation==='helix' && <Helix num={25}/>}
                {animation==='darklight' && <Darklight num={40}/>}
                {animation==='leafdance' && <Leafdance num={18}/>}
            </div> 

            <h3>Designs created with SASS and React</h3>
            <div className="radioBtnContainer">
                <div>
                    <label>
                        <input  type="radio" 
                                name="animation" 
                                value="prism" 
                                checked={animation==="prism"} 
                                onChange={click} />
                    Prism</label>
                </div>
                <div>
                    <label>
                        <input  type="radio" 
                                name="animation" 
                                value="helix" 
                                checked={animation==="helix"} 
                                onChange={click} />
                    Helix</label>
                </div>

                <div>
                    <label>
                        <input  type="radio" 
                                name="animation" 
                                value="darklight" 
                                checked={animation==="darklight"} 
                                onChange={click} />
                    Darklight</label>
                </div>
                <div>
                    <label>
                        <input  type="radio" 
                                name="animation" 
                                value="leafdance" 
                                checked={animation==="leafdance"} 
                                onChange={click} />
                    Leafdance</label>
                </div>
                <div>
                    <label>
                        <input  type="radio" 
                                name="animation" 
                                value="hide" 
                                checked={animation==="hide"} 
                                onChange={click} />
                    None</label>
                </div>

            </div>	
            <br />

        </div>
        )}
        </ThemeContextConsumer>
    )
}

export default AnimationRadio;